<template>
  <div class="home">
    <div class="nav-bar">
      <navBar/>
    </div>
    <cover/>
    <div class="article-list" v-if="articleList.length">
      <articleList :articleList='articleList' @article='gotoDetail'/>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import navBar from '@/components/navigatorBar.vue'
import cover from '@/components/cover.vue'
import articleList from '@/components/articleList.vue'
import article from '@/network/article'

export default {
  name: 'home',
  components: {
    navBar,
    cover,
    articleList
  },
  data () {
    return {
      articleList: []
    }
  },
  created () {
    this.getList()
  },
  methods: {
    async getList () {
      let res = await article.getArticleList()
      this.articleList = res.data
    },
    gotoDetail (id) {
      this.$router.push({
        path: `article/${id}`
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.nav-bar{
  position: fixed;
  top: 0;
  z-index: 10;
}
.article-list{
  max-width: 820px;
  margin: 0 auto;
}
</style>

