import axios from 'axios'

const baseUrl = 'http://47.107.142.136'

const get = (url, params) => {
    return axios.get(baseUrl + url, params)
        .catch(err => {
            throw err
        })
}

const post = (url, params) => {
    return axios.post(baseUrl + url, params, {
    })
        .catch(err => {
            throw err
        })
}

export default {
    get,
    post
}