import net from './index'

const getArticleList = async (page = 1) => {
    let res = await net.get(`/article`, {params: {page}})
        .catch(err => {
            throw err
        })
    return res
}

const getArticle = async (id) => {
    let res = await net.get(`/article/${id}`)
    return res
}

const saveArticle = async ({title, content, author, introduction}) => {
    let res = await net.post('/article', {
        title,
        content,
        author,
        introduction
    })
    return res
}

export default {
    getArticleList,
    getArticle,
    saveArticle
}