<template>
<div class="container">
    <div v-for="item in articleList" :key='item.article_id'>
        <h3 class="title">我的文章更新</h3>
        <h4 class="subtitle" @click="gotoDetail(item.title)">{{item.title}}</h4>
        <article class="description" @click="gotoDetail(item.title)">{{item.introduction}}</article>
        <p class="footer">posted by huidong on {{item.created_at}}</p>
    </div>   
</div>
</template>

<script>
export default {
    props: {
        articleList: {
            type: Array,
            required: true
        }
    },
    methods: {
        gotoDetail (id) {
            this.$emit('article', id)
        }
    }
}
</script>

<style lang="scss" scoped>
.container{
}
.title{
    text-align: left;
    margin: 12px 0;  
}
.subtitle{
    margin: 10px 0;
    text-align: left;
    font-weight: normal;
}
.subtitle:hover, .description:hover {
    color: rgb(61, 193, 233);
}
.description{
    text-align: left;
    font-style: italic;
    color: rgb(126, 126, 126);
}
.footer{
    text-align: left;
    margin: 6px 0;
    color: rgb(170, 169, 169);
}
</style>
